import React, { Component } from 'react';
import { Form, Table, Checkbox, Select } from 'antd';
import { InputNumberScore10 } from 'components/common';
import { getGradeNote } from 'services/gradeNote';

const { Option } = Select;
@Form.create({ name: 'formTrainingPlanTableDetail' })
class TrainingPlanTableDetail extends Component {
  state = { grandeNote: [] };
  componentDidMount() {
    this.getDataGrandeNote();
  }

  getDataGrandeNote = async () => {
    try {
      const dataNote = await getGradeNote();
      this.setState({ grandeNote: dataNote.data.items });
    } catch (error) {
      console.log(error);
    }
  };

  createColumns = () => {
    const { data } = this.props;
    const { grandeNote } = this.state;
    const columns1 = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'studentCode',
        key: 'studentCode',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        key: 'fullname',
      },
      {
        title: 'Không đủ ĐK',
        dataIndex: 'notQualified',
        key: 'notQualified',
        render: (_text, record) => {
          return <Checkbox checked={record.notQualified} disabled />;
        },
      },
    ];
    if (data && data.length && data[0].gradeParts && data[0].gradeParts.length) {
      data[0].gradeParts.forEach(gradePart => {
        const tempColumn = {
          title: gradePart.gradePartLabel,
          dataIndex: gradePart.gradePartId,
          key: gradePart.gradePartId,
        };
        columns1.push(tempColumn);
      });
    }
    const columns2 = [
      {
        title: 'TBCBP',
        dataIndex: 'subtotal',
        key: 'subtotal',
      },
      {
        title: 'Thi (60)',
        dataIndex: 'grade',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`grade-${record.studentId}_${record.studentId}`, {
                initialValue: record.grade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn điểm thi!',
                  },
                ],
              })(<InputNumberScore10 style={{ width: '75px' }} disabled={record.isLocked} />)}
            </Form.Item>
          );
        },
      },
      {
        title: 'Thang 4',
        dataIndex: 'totalIn4',
        key: 'totalIn4',
      },
      {
        title: 'TBC MH',
        dataIndex: 'total',
        key: 'total',
      },
      {
        title: 'Ghi chú',
        dataIndex: 'gradeNoteId',
        key: 'gradeNoteId',
        render: (_text, record) => {
          const { getFieldDecorator } = this.props.form;
          return (
            <Form.Item>
              {getFieldDecorator(`gradeNoteId-${record.studentId}_${record.studentId}`, {
                initialValue: record.gradeNoteId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn điểm ghi chú!',
                  },
                ],
              })(
                <Select disabled={record.isLocked}>
                  {grandeNote.map(item => (
                    <Option key={item.id} value={item.id}>
                      {item.label}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          );
        },
      },
    ];
    return columns1.concat(columns2);
  };

  render() {
    const { data, reload, rowKey } = this.props;

    return (
      <Table
        reload={reload}
        columns={this.createColumns()}
        dataSource={data}
        scroll={{ x: true }}
        rowKey={rowKey}
      />
    );
  }
}

export default TrainingPlanTableDetail;



